/*=============================================
=            Variables
=============================================*/

/*----------  General Colors  ----------*/
$color-black:       #20292a !default;
$color-black--text: #5b5b5b;
$color-white:       #fff !default;
$color-grey:        #f0f0f0 !default;
$color-border:      rgba($color-black, 0.2) !default;
$color-hover:       rgba($color-black, 0.03) !default;

/*----------  Brand Colors  ----------*/
$color-brand-primary:       #0090de !default;
$color-brand-primary--dark: #484848 !default;
$color-brand-accent:        #f7256c !default;
$color-brand-white:  $color-grey !default;

/*----------  Animation Properties  ----------*/

$animation-easing: ease-out !default;
$animation-duration: 0.35s !default;
$animation-delay: 0.2s !default;
$animation-easing-max-height: cubic-bezier(0, 1, 0, 1) !default;

/*----------  Subsection comment block  ----------*/

$z-index-header: 10 !default;

/*----------  Dimensions  ----------*/
$dimen-content-width: 1000px !default;
$dimen-unit:          6px !default;
$dimen-padding:       $dimen-unit * 2 !default;
$dimen-gutter:        $dimen-unit * 2 !default;
$dimen-border-radius: 3px !default;

$shadow:              0 1px 4px 2px rgba($color-black, 0.03) !default;
$border:              $color-border 1px solid !default;

/*----------  Header  ----------*/

$header-height:           60px !default;
$header-padding:          6px !default;
$header-background-color: $color-white !default;
$header-logo-max-width:   200px !default;
$header-logo-max-height:  $header-height - $header-padding * 2 !default;
$header-hamburger-size:   24px !default;
$header-hamburger-padding: 0px !default;
$header-hamburger-color: $color-brand-primary !default;
$header-hamburger-background: $color-grey !default;
$header-hamburger-stroke-size: 1px !default;
$header-hamburger-stroke-width: $header-hamburger-size !default;
$header-hamburger-background-radius: $dimen-border-radius !default;
$header-hamburger-stroke-radius: 0px !default;
$header-is-absolute-nav: false !default;
$header-nav-max-height: 500px !default;

$button-max-width: 360px;

/*----------  Body  ----------*/

$body-background-image: !default;
$body-background-color: $color-white !default;

/*----------  Footer  ----------*/

$footer-background-color: $color-grey !default;

/*----------  Font Variables  ----------*/

// Base font styles
$font-base-family:             "Helvetica Neue", Helvetica, sans-serif !default;
$font-base-size:               14px !default;
$font-base-weight:             300 !default;
$font-base-height:             1.5 !default;

$base-font-size--small: 12px;

$font-top-offset: 0 !default;
$font-bottom-offset: 0 !default;

// Links styles
$font-link-family:             $font-base-family !default;
$font-link-weight:             normal !default;
$font-link-size:               $font-base-size !default;

// Heading Styles
$font-heading-family:          $font-base-family !default;
$font-heading-weight:          normal !default;
$font-heading-size--primary:   32px !default;
$font-heading-size--secondary: 24px !default;
$font-heading-size:            18px !default;
$font-heading-size--small:     24px !default;


/*----------  Lists  ----------*/

$list-title-size: $font-heading-size !default;
$list-text-size: $font-base-size !default;
$list-line-height: 24px !default;

/*----------  Form Elements  ----------*/

$form-input-height:             36px !default;
$form-input-border-size:        1px !default;
$form-input-padding-vertical:   6px !default;
$form-input-padding-horizontal: 8px !default;
$form-input-padding:            $form-input-padding-vertical $form-input-padding-horizontal;
$form-input-line-height:        $form-input-height - $form-input-padding-vertical * 2 !default;
$form-input-border-color:       $color-border !default;
$form-input-border:             $form-input-border-size $form-input-border-color solid;
$form-input-text-size:          $font-base-size !default;
$form-border-radius:            3px !default;

// Buttons
$form-button-background-color: $color-brand-primary !default;
$form-button-text-color:       $color-white !default;
$form-button-font-family:      $font-base-family !default;
$form-button-font-weight:      bold !default;
$form-button-font-size:        $font-base-size !default;
$form-button-padding:          12px !default;
$form-button-border-radius:    $form-border-radius !default;
$form-button-height:           $form-input-height !default;
$form-button-border-color:     darken($form-button-background-color, 0.03) !default;
$form-button-border-size:      $form-input-border-size !default;
$form-button-border:           $form-input-border-color $form-input-border-size solid !default;

/*----------  Message  ----------*/

$message-text-color: $color-black--text;
$message-text-color--admin: $message-text-color;
$message-text-color--moderator: $message-text-color;
$message-code-background: rgba($color-black, 0.9);
$message-code-color: $color-white;

/*----------  Media Query Breakpoints  ----------*/

$breakpoint-mobile:         350px !default;
$breakpoint-mobile--large:  410px !default;
$breakpoint-tablet--small:  600px !default;
$breakpoint-tablet:         850px !default;
$breakpoint-desktop:        1000px !default;
$breakpoint-desktop--large: 1200px !default;
$breakpoint-default:        $breakpoint-tablet--small !default;

/*----------  Image Buttons  ----------*/

/*=====  End of Variables  ======*/
