.c_Frame {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    background: $color-white;
}

.c_Frame-top {
    flex: 1 0 auto;
}

.c_Frame-content {
    padding-top: 12px;
    padding-bottom: 12px;
}

.c_Frame-contentWrap {
    background: $color-white;
}

.c_Frame-details {
    @include clearfix;
}

.c_Container {
    @include gutter;
    max-width: $dimen-content-width;
    margin: 0 auto;
}

.c_Frame-row {
    width: 100%;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    justify-content: space-between;
}

.c_Frame-content {
    .DataListWrap {
        margin-top: 24px;
    }

    .SearchBox {
        max-width: 400px;
        width: 100%;
        margin-bottom: 12px;
    }

    .Breadcrumbs {
        margin-bottom: 6px;
        display: inline-block;
        margin-right: 12px;

        a {
            text-transform: uppercase;
            // font-weight: bold;
            font-size: $font-base-size - 2px;
            color: $color-brand-primary;
        }

        .Last a,
        .Crumb {
            color: $color-black--text;
        }
    }

    h1 {
        font-weight: bold;
        font-size: $font-heading-size--secondary;
        margin-bottom: 6px;
    }
}

@include min-width($breakpoint-tablet) {
    .c_Content {
        width: 75%;
        float: left;
    }

    .c_Panel {
        width: 25%;
        float: right;
        padding-left: 18px;
    }
}
